// @flow

import React, {useState, useCallback} from 'react'
import {useStaticQuery, graphql} from 'gatsby'
import styled, {ThemeProvider} from 'styled-components'
import {AppBar, Toolbar, CssBaseline, IconButton, Typography} from '@material-ui/core'
import {Menu} from '@material-ui/icons'
import {useTheme} from '@material-ui/core/styles'

import NavigationDrawer from './NavigationDrawer'

declare var __PATH_PREFIX__: any

type Props = {
  location: {
    pathname: string,
  },
  title: string,
  children: any,
}

const FONT_NANUM_GOTHIC: string = 'Nanum Gothic'

// const Container = styled.div`
//   margin-left: auto;
//   margin-right: auto;
//   max-width: ${rhythm(24)};
//   padding: ${rhythm(1.5)} ${rhythm(3 / 4)};
// `

const MyFooter = styled.footer`
  font-family: ${FONT_NANUM_GOTHIC};
`

const Root = styled.div`
  flex-grow: 1;
  z-index: 1;
  overflow: hidden;
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
`

const MyAppBar = styled(AppBar)``

const MyIconButton = styled(IconButton)`
  margin-right: 30px;
`

const Layout = (props: Props) => {
  const theme = useTheme()

  const data = useStaticQuery(graphql`
    query AuthorQuery {
      site {
        siteMetadata {
          author {
            name
          }
        }
      }
    }
  `)

  const {location, title, children} = props
  const [navOpen, setNavOpen] = useState<boolean>(false)

  const handleOnNavClose = useCallback(() => {
    setNavOpen(false)
  }, [])

  const handleOnMyIconClick = useCallback(() => {
    setNavOpen(true)
  }, [])

  // const rootPath = `${__PATH_PREFIX__}/`

  console.log('root path', `${__PATH_PREFIX__}/`)
  console.log('pathname', location.pathname)

  const {author} = data.site.siteMetadata

  return (
    <>
      <CssBaseline />
      <ThemeProvider theme={theme}>
        <Root>
          <MyAppBar position="absolute">
            <Toolbar>
              <MyIconButton color="inherit" aria-label="Open drawer" onClick={handleOnMyIconClick}>
                <Menu />
              </MyIconButton>
              <Typography variant="h6" color="inherit" noWrap>
                {title}
              </Typography>
              <div style={{flex: 1}} />
            </Toolbar>
          </MyAppBar>
          <NavigationDrawer location={location} open={navOpen} onClose={handleOnNavClose} />
          <main>{children}</main>
          <MyFooter>
            © {new Date().getFullYear()}, Built with
            {` `}
            <a href="/">{author.name}</a>
          </MyFooter>
        </Root>
      </ThemeProvider>
    </>
  )
}

export default Layout
